<template>
  <div class="app-container">
    <div style="float: left;margin-bottom: 20px">
      <el-input v-model="listQuery.tableName" clearable placeholder="请输入表名" style="width: 200px;"  @keyup.enter.native="handleFilter" />
      <el-button  type="primary" plain @click="handleFilter" >搜索</el-button>
      <el-button  type="primary" plain @click="handleCreate" >生成</el-button>
    </div>
    <el-table
      :data="list"
      border
      ref="multipleTable"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        label="表名"
        align="center"
        width="600">
        <template slot-scope="scope">
          <el-tag size="medium">{{ scope.row.tableName }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label="创建时间"
        align="center"
        width="600">
        <template slot-scope="scope">
          <i class="el-icon-time"></i>
          <span style="margin-left: 10px">{{ scope.row.createTime }}</span>
        </template>
      </el-table-column>
    </el-table>
    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit"
                @pagination="getList"/>
  </div>
</template>

<script>
  import {createTemplate, fetchTable, fetchTableInfo} from "../api/table";
import Pagination from '@/components/Pagination'
  import axios from 'axios'
export default {
  name: 'HelloWorld',
  components: {Pagination},
  data () {
    return {
      msg: '生成工具',
      listQuery: {
        page: 1,
        limit: 10,
        tableName: null,
      },
      list: null,
      total: 0,
      tableData:[],
      dialogFormVisible:false,
      type:[
        {
        id:1, name:"input",label:"输入框"
      },
        {
          id:2, name:"select",label:"下拉选"
        },
        {
          id:3, name:"radio",label:"单选框"
        },
      ],
      multipleSelection: []
    }
  },
  created:function(){
    this.getList()
  },
  methods:{
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log(this.multipleSelection)
    },
    handleCreate(){
      let data={
        tableData:this.multipleSelection
      }
      axios({ // 用axios发送post请求
        method: 'post',
        url: "/base/generator/create", // 请求地址
        data: data, // 参数
        responseType: 'blob', // 表明返回服务器返回的数据类型
        headers:{
          'Content-Type': 'application/json; application/octet-stream'
        },
      })
        .then((res) => { // 处理返回的文件流
          this.dialogFormVisible = false
          const blob = new Blob([res.data]);
          const downloadElement = document.createElement("a");
          const href = window.URL.createObjectURL(blob);
          //后台再header中传文件名
          const name = "code.zip";
          downloadElement.href = href;
          downloadElement.download = name;
          document.body.appendChild(downloadElement);
          downloadElement.click();
          document.body.removeChild(downloadElement); // 下载完成移除元素
          window.URL.revokeObjectURL(href); // 释放掉blob对象
        })
    },

    addData(type) {
      switch (type) {
        case 0:
          this.formData.searchData.push({
            name:null,
            type:null
          });
          return;
        case 1:
          this.formData.createData.push({
            name:null,
            type:null
          });
          return;
      }

    },
    handleEdit(row){
      this.formData={
        tableName:row.tableName,
        searchData:[{
          name:null,
          type:null
        }],
        createData:[{
          name:null,
          type:null
        }],
        tableData:[],
        dealData:[]
      };
      const tableName={tableName:row.tableName};
      fetchTableInfo(tableName).then(response=>{
          this.tableData=response.data;
          this.dialogFormVisible=true;
      })
    },
    removeData(item,type) {
      switch (type) {
        case 0:
          var index = this.formData.searchData.indexOf(item)
          if (index !== -1) {
            this.formData.searchData.splice(index, 1)
          }
          return;
        case 1:
          var index = this.formData.createData.indexOf(item)
          if (index !== -1) {
            this.formData.createData.splice(index, 1)
          }
          return
      }

    },
    handleFilter() {
      this.listQuery.page = 1
      this.getList()
    },
    getList(){
      fetchTable(this.listQuery).then(response => {
        let data=response.data.data;
        this.list=data.rows
        this.total=data.total
      })
    },
  }
}
</script>





