import request from '@/utils/request'
import Qs from 'qs'

export function fetchTable(params) {
  return request({
    url: '/base/generator/page',
    method: 'get',
    params
  })
}

export function fetchTableInfo(params) {
  return request({
    url: '/base/generator/getTable',
    method: 'get',
    params
  })
}

export function createTemplate(params) {
  return request({
    url: '/base/generator/create',
    method: 'post',
    data: params,
    responseType: 'blob'
  })
}



